embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  /* border: 1px solid red; */
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

@font-face {
  font-family: "Roboto Flex";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

:root {
  --MAIN-COLOUR: #fafaf9;
  --BG-COLOUR: #e5e7eb;
  --TEXT-LIGHT: #a3a3a3;
  --ACCENT-1: #0ea5e9;
  --ACCENT-2: #facc15;
  font-family: "Roboto Flex", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

body {
  background-size: cover;
}

.gif-blackout {
  background-color: rgba(0, 0, 0, 0.5);
}

.content {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: auto;
}

.small-weather-icon {
  width: 50px;
  height: 50px;
}

.forecast-window {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 3rem;
}

/* OPTIONS ROW */

.options-row {
  width: 95%;
  align-self: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* LOCATION FORM */

#locationForm {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

#locationInput {
  background-color: var(--MAIN-COLOUR);
  font: inherit;
  border: none;
  border-radius: 1rem;
  padding: 0.3rem 0.8rem;
}

#locationInput:focus {
  outline: 3px solid var(--ACCENT-1);
}

/* TEMP BUTTONS */

.temp-button-wrapper {
  display: flex;
  align-self: flex-end;
}

.temp-btn,
#locationSubmit {
  align-self: flex-end;
  background-color: var(--ACCENT-1);
  color: var(--MAIN-COLOUR);
  width: fit-content;
  font: inherit;
  font-weight: bold;
  cursor: pointer;
  border: 3px solid black;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
}

/* FORECAST CARD */

.forecast-card {
  background-color: black;
  height: 500px;
  width: 700px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 2fr 1fr;
  gap: 0.2rem;
  border: 3px solid black;
  border-radius: 2rem;
  overflow: hidden;
}

/* CURRENT HOUR */

.current-hour-wrapper {
  background-color: darkgray;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 3rem;
  padding-top: 1.2rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
}

.current-hour-header {
  grid-column: 1/3;
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
}

.current-hour-header > h1 {
  color: var(--MAIN-COLOUR);
  font-size: 2rem;
  font-weight: bold;
  text-transform: uppercase;
}

.current-hour-forecast {
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather-icon {
  width: 200px;
  height: 200px;
}

.current-hour-stats {
  display: grid;
  grid-template-rows: 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.current-hour-stats > div {
  background-color: grey;
  min-width: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  padding: 1rem;
}

.current-hour-stats {
  color: var(--MAIN-COLOUR);
  font-size: 2.8rem;
  font-weight: bold;
}

/* NEXT HOURS */

.next-hours-wrapper {
  background-color: var(--ACCENT-1);
}

.next-hours-bg {
  background-color: black;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.next-hour-card {
  flex: 1;
  background-color: var(--ACCENT-1);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  align-items: center;
  padding: 0.5rem;
}

.next-hour-card > p {
  color: var(--MAIN-COLOUR);
  font-weight: bold;
}

.next-hour-card > img {
  filter: invert(83%) sepia(63%) saturate(2%) hue-rotate(20deg) brightness(105%)
    contrast(98%);
}

/* WEEK WRAPPER */

.week-wrapper {
  grid-column: 1 / 3;
  background-color: var(--ACCENT-2);
}

.day-card-bg {
  height: 100%;
  background-color: black;
  display: flex;
  gap: 0.2rem;
}

.day-card {
  flex: 1;
  background-color: var(--ACCENT-2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
}

.day-card > p {
  font-weight: bold;
}

.weekday {
  text-transform: uppercase;
}

.day-card img {
  margin-bottom: 5px;
}

.max-temp {
  font-size: 1.2rem;
  margin-bottom: -3px;
}

.min-temp {
  color: var(--MAIN-COLOUR);
}
